import { connect } from 'react-redux';
import { FilterType, actions } from '../../redux/userReducer';

import {
  // actions.toggleIsFollowing,
  getUsers,
  followAPI,
  unfollowAPI,
} from '../../redux/userReducer';
import React from 'react';
import Users from './Users';

import Loader from '../common/Loader/Loader';
import withAuthRedirect from '../Hoc/withAuthRedirect';
import { compose } from 'redux';
import { userItem } from '../../Types/types';
import {
  getIsFetching,
  getIsFollowingInProgress,
  getPageSelected,
  getPageSize,
  getTotalUsers,
  getUsersFilter,
  getUsersItems,
} from '../../redux/userSelector';
import { AppStateType } from '../../redux/reduxStore';
const toggleIsFollowing = actions.toggleIsFollowing;

type MapStateType = {
  pageSelected: number;
  pageSize: number;
  isFetching: boolean;
  totalUsers: number;
  usersItems: Array<userItem>;
  isFollowingInProgress: number[];
  filter: FilterType;
};

type MapDispatchType = {
  getUsers: (
    pageSelected: number,
    pageSize: number,
    filter: FilterType,
  ) => void;
  followAPI: (userId: number) => void;
  unfollowAPI: (userId: number) => void;
};

type Props = MapStateType & MapDispatchType & React.ComponentType;

class UsersAPIContainer extends React.Component<Props> {
  componentDidMount() {
    let { pageSelected, pageSize, filter } = this.props;
    this.props.getUsers(pageSelected, pageSize, filter);
  }

  onPageChanged = (p: number) => {
    let { pageSize, filter } = this.props;
    this.props.getUsers(p, pageSize, filter);
  };
  onFilterChanged = (filter: FilterType) => {
    let { pageSize } = this.props;
    debugger;
    this.props.getUsers(1, pageSize, filter);
  };

  render() {
    return (
      <>
        {this.props.isFetching ? <Loader /> : null}
        <Users
          onPageChanged={this.onPageChanged}
          onFilterChanged={this.onFilterChanged}
          totalUsers={this.props.totalUsers}
          pageSize={this.props.pageSize}
          pageSelected={this.props.pageSelected}
          usersItems={this.props.usersItems}
          isFollowingInProgress={this.props.isFollowingInProgress}
          followAPI={this.props.followAPI}
          unfollowAPI={this.props.unfollowAPI}
        />
      </>
    );
  }
}

let mapStateToProps = (state: AppStateType): MapStateType => {
  return {
    filter: getUsersFilter(state),
    usersItems: getUsersItems(state),
    totalUsers: getTotalUsers(state),
    pageSize: getPageSize(state),
    pageSelected: getPageSelected(state),
    isFetching: getIsFetching(state),
    isFollowingInProgress: getIsFollowingInProgress(state),
  };
};

export default compose<Props>(
  withAuthRedirect,
  connect(mapStateToProps, {
    toggleIsFollowing,
    getUsers,
    followAPI,
    unfollowAPI,
  }),
)(UsersAPIContainer);
