import React from 'react';
import s from './Users.module.css';

import Paginator from '../common/Paginator/Paginator';
import User from './User/User';
import { userItem } from '../../Types/types';
import { Field, Form, Formik } from 'formik';
import { FilterType } from '../../redux/userReducer';

type Props = {
  totalUsers: number;
  pageSize: number;
  pageSelected: number;
  usersItems: Array<userItem>;
  isFollowingInProgress: number[];
  onPageChanged: (p: number) => void;
  onFilterChanged: (filter: FilterType) => void;
  unfollowAPI: (userId: number) => void;
  followAPI: (userId: number) => void;
};

let Users: React.FC<Props> = (props) => {
  return (
    <div className={s.allUsers}>
      <UsersForm onFilterChanged={props.onFilterChanged} />
      <Paginator
        totalUsers={props.totalUsers}
        pageSize={props.pageSize}
        pageSelected={props.pageSelected}
        onPageChanged={props.onPageChanged}
      />
      <div className={s.item}>  </div>
      {props.usersItems.map((item) => (
        <User
          key={item.id}
          item={item}
          isFollowingInProgress={props.isFollowingInProgress}
          unfollowAPI={props.unfollowAPI}
          followAPI={props.followAPI}
        />
      ))}
    </div>
  );
};

const usersFormValidation = (values: any) => {
  const errors = {};
  return errors;
};

type FormProps = {
  onFilterChanged: (filter: FilterType) => void;
};

type FormType = {
  term: string;
  friend: 'true' | 'false' | 'null';
};

const UsersForm: React.FC<FormProps> = (props) => {
  const submit = (
    values: FormType,
    { setSubmitting }: { setSubmitting: (isSubmitting: boolean) => void },
  ) => {
    // setTimeout(() => {
    //   alert(JSON.stringify(values, null, 2));
    //   setSubmitting(false);
    // }, 400);
    //  "false"   false
    const filter = {
      term: values.term,
      friend:
        values.friend === 'null'
          ? null
          : values.friend === 'false'
          ? false
          : true,
    };

    props.onFilterChanged(filter);
    setSubmitting(false);
  };

  return (
    <>
      <Formik
        initialValues={{ term: '', friend: 'null' as const }}
        validate={usersFormValidation}
        onSubmit={submit}
      >
        {({ isSubmitting }) => (
          <Form>
            <Field type="text" name="term" />
            <Field name="friend" as="select">
              <option value="null"></option>
              <option value="true"></option>
              <option value="false"> </option>
            </Field>
            <button type="submit" disabled={isSubmitting}>
              
            </button>
          </Form>
        )}
      </Formik>
    </>
  );
};

export default Users;
